import Navigation from './Navigation'
import Performance from '../performance/Performance'
import Population from '../population/Population'
import { BrowserRouter as Router, Switch, Route, Redirect } from "react-router-dom";

function Routes() {
    return (
      <div>
        <Navigation />
        <Router>
            <Switch>
              <Route exact path="/DacaAnalytics/population" component={Population} />
              <Route exact path="/">
                <Redirect to="/DacaAnalytics/population" />
              </Route>
              <Route exact path="/DacaAnalytics/performance" component={Performance} />
            </Switch>
          </Router>
      </div>
    );
  };

export default Routes;